import React, { useState, useEffect } from 'react'
import {
  CBadge,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
  CFormGroup
} from  '@coreui/react'
import CIcon from '@coreui/icons-react'
import { httpClient } from '../../helpers';
import ReactJson from 'react-json-view'

const Transactions = (props) => {
    const [fetch, setFetch] = useState(true);
    const [transaction, setTransaction] = useState(true);
    const [jsonObj, setJsonObj] = useState({})
    const [invoiceLink, setInvoiceLink] = useState("")

    useEffect(() => {
        if(fetch) {
          const url = "/be/api/transaction/result/" + props.match.params.id ;
          httpClient
            .get(url)
            .then((res) => {
                console.log(res.data.claim);
                setTransaction(res.data);
                setJsonObj(res.data.claim);
                setInvoiceLink('/invoice/' + res.data.identifier);
            })
            .catch((error) => {
              console.error("Error", error);
            });
          
          setFetch(false);
        }

      },[fetch]);

  return (
    <>
      <CRow>
        <CCol xs="12" sm="4" md="4">
          <CCard>
            <CCardHeader>
              Transaction
              <div className="card-header-actions">
                <CBadge color="success" className="float-right">{transaction.identifier}</CBadge>
              </div>
            </CCardHeader>
            <CCardBody>
                <CFormGroup>
                <div>
                    <div className="small text-muted">Created At</div>
                    <strong>{transaction.created_at}</strong>
                </div>
                </CFormGroup>
                <CFormGroup>
                <div>
                    <div className="small text-muted">Client Code</div>
                    <strong>{transaction.clientcode}</strong>
                </div>
                </CFormGroup>
                <CFormGroup>
                <div>
                    <div className="small text-muted">File Name</div>
                    <strong>{transaction.filename}</strong>
                </div>
                </CFormGroup>
                <CFormGroup>
                <div>
                    <div className="small text-muted">Item Success Read</div>
                    <strong>{transaction.line_item_ok}</strong>
                </div>
                </CFormGroup>
                <CFormGroup>
                <div>
                    <div className="small text-muted">Item Error Read</div>
                    <strong>{transaction.line_item_ex}</strong>
                </div>
                </CFormGroup>
                <div>
                  <a href={invoiceLink}>{transaction.identifier}</a>
                </div>
            </CCardBody>
          </CCard>
        </CCol>
        <CCol xs="12" sm="8" md="8">
          <CCard>
            <CCardHeader>
              Data
            </CCardHeader>
            <CCardBody>
              <ReactJson src={jsonObj} />
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
    </>
  )
}

export default Transactions
